.movieCard {
  list-style: none;
  font-size: 1.5rem;
  text-align: center;
}

.movieCard:hover {
  opacity: 0.8;
}

.movieImage {
  border-radius: 10px;
  background-color: rgb(130, 197, 197);
}

@media (max-width: 560px) {
  .movieImage {
    width: 90%;
    height: auto;
  }
}
